  cursor: pointer;
  width: 331.772px;
  position: relative;
  transition: 0.3s;
}
.creator__video:hover .creator__video__dots-wr {
  display: flex;
}
.creator__video:hover .creator__video__bottom {
  background-color: #222222;
  border-radius: 0 0 7.212px 7.212px;
}
.creator__video:hover::after {
  pointer-events: none;
  content: "";
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-repeat: no-repeat;
  display: block;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, 0);
  width: 50px;
  transition: 0.3s;
  height: 40px;
}
.creator__video__edit {
  position: absolute;
  right: 15px;
  top: 10px;
  z-index: 1;
}
.creator__video__dots-wr {
  border-radius: 7.212px;
  background: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(1.8031088114px);
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s;
  display: none;
}
.creator__video__dots-wr.active {
  border: 1px solid #fff;
}
.creator__video__dots-wr:hover {
  border: 1px solid #fff;
}
.creator__video__preview {
  width: 100%;
  height: 188.049px;
  object-fit: contain;
  background-color: #222;
  position: relative;
}
.creator__video__bottom {
  transition: 0.3s;
  padding: 7px;
}
.creator__video__bottom_title {
  font-size: 14.425px;
  font-weight: 500;
  line-height: 16.228px;
}
.creator__video__bottom_date {
  font-size: 12.622px;
  font-weight: 400;
  margin-top: 12px;
}
.creator__video_view-later {
  position: absolute;
  right: 0;
  bottom: -35px;
  width: 187px;
  background: red;
  border-radius: 8px;
  padding: 10px;
  cursor: pointer;
  background: var(--9, #222);
  display: none;
}
.creator__video_view-later:hover {
  text-decoration: underline;
}
.creator__video_view-later.active {
  display: block;
}
.creator__video_author {
  display: flex;
  align-items: center;
  gap: 5px;
}
.creator__video_author-wr {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 5px;
}
.creator__video_author-wr .creator__video__bottom_date {
  margin-top: 0;
}
.creator__video_author_text {
  color: #999;
  font-size: 14px;
  font-weight: 400;
}
.creator__video_author_img {
  width: 24px;
  height: 24px;
}

.video-cr-playlist {
  position: static;
}
.video-cr-playlist .creator__video__edit {
  display: none;
}
.video-cr-playlist:hover::after {
  display: none;
  content: "";
  background: none;
}

@media screen and (max-width: 375px) {
  .creator__video {
    width: 343px;
  }
